<template>
  <div class="hello">
    <h1>View images</h1>
    <!-- {{ds}}
    [ "208da5a3-7282-4df5-99cb-2ab76dd76e9f_thuc101_avatar02.jpg", "28b23     -->
    <b-row class="border-bottom mb-3">
      <b-col md="1" v-for="x in ds" :key="x">
        <div class="border mb-2 position-relative bg-dark text-light" style="height:50px;">
          <img width="100%" height="50px" :src="`/api/FileUpload/GetImage/${x}`" alt="Image 1" />
          <span class="position-absolute" style="bottom:1px;right:1px;">
            <a href="#" @click="onDel(x)">Del</a>
          </span>
        </div>
      </b-col>
    </b-row>

  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      ds: []
    }
  },
  mounted() {
    this.loadListImg();
  },
  methods: {
    loadListImg() {
      this.getListImg().then((res) => {
        this.ds = res.data;
      });
    },
    async getListImg() {
      return await axios.get(`/api/FileUpload/GetListFile`).catch((err) => {
        console.log(err);
      });
    },
    onDel(x) {
      this.Del(x).then(res => {
        this.loadListImg();
      })
    },
    async Del(fileName) {
      return await axios
        .get(`/api/FileUpload/Del/${fileName}`)
        .catch((err) => {
          console.log(err);
        });
    },



  }
}
</script>
