<template>
  <div class="MonHocToan">
    <raw-loader :mytxt="require('!raw-loader!./AxiosUploadBase64.vue').default"></raw-loader>
    <h1>AxiosUploadBase64 page</h1>
    <div>
      <b-row>
        <b-col md="3" class="border-right">
          <form enctype="multipart/form-data" @submit.stop.prevent="onUpload">
            <b-img ref="output" fluid alt="Responsive image"></b-img><br>
            <input type="file" id="file" ref="file" class="m-1" @change="onFileChange" /><br>
            <div>
              <button type="submit" class="mr-1">Upload</button>
              <button @click="onRemove" class="mr-1">Remove</button>
            </div>
          </form>
        </b-col>
        <b-col md="9">
          <com-view-imag ref="listImg"></com-view-imag>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
import $ from "jquery"
import axios from "axios";
import ComViewImag from "./ComViewImage.vue";
export default {
  name: 'Tpl',
  components: {
    ComViewImag
  },
  methods: {

    onUpload() {
      let files = this.$refs.file.files;
      if (files.length == 0) return;
      let file = files[0];
      let that = this;
      this.getReadAsDataURL(file).then((res) => {
        let fd = new FormData();
        fd.append("base64", res);
        that.UploadAxios2(fd).then(res2 => {
          that.$refs.listImg.loadListImg();
          $(that.$refs.output).attr("src", "");
          $(that.$refs.file).val("");
        })
      });
    },
    onFileChange() {
      let files = this.$refs.file.files;
      let file = files[0];
      this.getReadAsDataURL(file).then((res) => {
        $(this.$refs.output).attr("src", res);
      });
    },
    onRemove() {
      $(this.$refs.output).attr("src", "");
      $(this.$refs.file).val("");
    },
    getReadAsDataURL(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.onload = () => {
          resolve(reader.result);
        };
        reader.onerror = reject;
        reader.readAsDataURL(file);
      });
    },
    async UploadAxios(formData) {
      return await axios
        .post(`/api/FileUpload/UploadAxios`, formData)
        .catch((err) => {
          console.log(err);
        });
    },
    async UploadAxios2(formData) {
      return await axios
        .post(`/api/FileUpload/UploadAxios2`, formData)
        .catch((err) => {
          console.log(err);
        });
    },


  }
}
</script>
